 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 10:39:02
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">套餐维护</div>
      <div class="head-btn"
        v-if="btns.hasInsert"
        data-btn-id='106007001'
        @click="$router.push({name: 'SetMealForm', params: {id: 'insert'}})">
        <img src="@/assets/img/add.png"
          alt="登记">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='套餐名称：'>
          <el-input placeholder="请输入"
            v-model="querys.packName"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='所属机构名称：'>
          <el-select v-model="querys.createOrganizationId"
            clearable>
            <el-option v-for="item in orgList"
              :key='item.sysGuid'
              :value="item.sysGuid"
              :label="item.oname" />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="$refs.pagination.initPageList(1)">查询</el-button>
        </el-form-item>

        <el-form-item label="显示状态："
          style="float:right;margin-right:0">
          <el-radio-group class="red"
            v-model="querys.enabled"
            @change="$refs.pagination.initPageList(1)">
            <el-radio label="">全部</el-radio>
            <el-radio label="1">已启用</el-radio>
            <el-radio label="2">已禁用</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading='loading'
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='{$index}'>
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label='机构名称'
          prop="oname"
          show-overflow-tooltip></el-table-column>
        <el-table-column label='套餐顺序'
          prop='sortNum'
          width="84"></el-table-column>
        <el-table-column label='套餐编号'
          prop='sysGuid'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='套餐名称'
          prop='packName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='套餐状态'
          width="84">
          <template v-slot="{row}">
            <span :class="[row.enabled == 1 ? 'status-enable' : 'status-disable']"></span>
          </template>
        </el-table-column>
        <el-table-column label='创建人员'
          prop='createUserName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='创建日期'
          width="120">
          <template v-slot="{row}">
            {{row.createDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template v-slot="{row}">
            <i class="iconfont bj-search"
              @click="$router.push({name: 'SetMealSee', params: {id: row.sysGuid}})"></i>
            <i class="iconfont bj-edit"
              v-if="btns.hasUpdate&&row.enabled==1"
              data-btn-id='106007002'
              @click="$router.push({name: 'SetMealForm', params: {id: row.sysGuid}})"></i>
            <i class="iconfont bj-delete"
              v-if="btns.hasDel&&row.enabled==1"
              data-btn-id='106007003'
              @click="delConfirm(row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination query-url='/sys/projectPackage/listPaging'
        :query-obj='querys'
        @listResult='getListResult'
        @controlLoading='loading=$event'
        ref="pagination" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'SetMealList',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      querys: {
        packName: '',
        enabled: '',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,
      loading: true
    }
  },

  computed: {
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('106007001'), // 新增按钮权限
        hasUpdate: ~btnIds.indexOf('106007002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('106006003') // 删除按钮权限
      }
    },
    ...mapState(['orgList'])
  },

  methods: {
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    delConfirm({ sysGuid }) {
      this.$confirm('', {
        title: '确定删除记录？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/sys/projectPackage/remove', { sysGuid })
            .then(res => {
              this.$message.success('删除成功')
              let pagination = this.$refs.pagination
              if (this.tableData.length > 1) {
                return pagination.initPageList()
              }
              let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
              pagination.initPageList(pageNum)
            })
            .catch(err => {
              console.log(err)
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}

.table-box {
  padding: 0 20px;
  overflow: hidden;
}
</style>



